import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";

class Profile extends Component {
  constructor(props) {
    super(props);

    this.state = {
      profile: [],
      posts: [],
      roles: []
    };
  }

  componentDidMount() {
    axios
      .get("/profile")
      .then(res => {
        this.setState({ profile: res.data });
      })
      .catch(err => console.log(err));

    axios
      .get("/posts")
      .then(res => {
        this.setState({ posts: res.data });
      })
      .catch(err => console.log(err));

    axios
      .get("/roles")
      .then(res => {
        this.setState({ roles: res.data });
      })
      .catch(err => console.log(err));
  }

  render() {
    return (
      <div className="container pt-5">
        {this.state.profile.map(p => (
          <div>
            {this.props.auth.users == p.accountId ? (
              <div>
                <div className="profile-img">
                  <img className="img-responsive" src={`${p.bgPic}`} />
                  <img
                    className="img-thumbnail"
                    height="100px"
                    width="100px"
                    src={`${p.profilePic}`}
                  />
                </div>
                <p className="lead pt-5" style={{ marginTop: "50px" }}>
                  {p.bioInfo}
                </p>
              </div>
            ) : (
              <p className="lead pt-3 ">
                No profile, create one <Link to="/dashboard">here</Link>
              </p>
            )}
          </div>
        ))}

        <h2 className="display-4 pt-5 pb-3" style={{ marginTop: "50px" }}>
          Check out my posts!
        </h2>

        {this.state.posts.length == undefined ? (
          <p className="lead pt-5 pb-3">
            No posts, create one <Link to="/create">here</Link>
          </p>
        ) : (
          <div>
            {this.state.posts.map(p => (
              <div>
                {this.props.auth.users == p.accountId ? (
                  <div>
                    <Link to={`/show/${p.id}`}>
                      <img
                        style={{ display: "block" }}
                        className="mr-auto ml-auto"
                        src={`${p.imageURL}`}
                        alt=""
                      />
                    </Link>
                    <h3 className="font-weight-bold pt-2">{p.title}</h3>
                    <p className="lead pt-1">{p.subTitle}</p>
                    <p className="text-muted pt-3">Created by: {p.name}</p>
                    <hr className="pt-2 pb-3" />
                  </div>
                ) : (
                  <p className="lead pt-5 pb-3">
                    No posts, create one <Link to="/create">here</Link>
                  </p>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    );
  }
}

Profile.propTypes = {
  auth: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth
});

export default connect(mapStateToProps)(Profile);
